<template>
	<div>
		<div class="title_pic"></div>
		<p class="title">
			每日一练说明:
		</p>
		<div class="instruction">
			
			
		</div>
		<p class="title">班级学习进度: (题库总数量: {{list.libraryNum}})</p>
		<p class="little">{{list.schedule}}</p>
		<p class="title">目前已累计刷题量:</p>
		<p class="little" style="margin-bottom: 0.4rem;">{{list.finishNum}}</p>
		<div class="selectBox">
			<div :class="['selectIcon',activeIndex==0?'active':'']" @click="change(0)">
				<img v-if="activeIndex==0" src="../../../static/right.png" />
			</div>
			<span>允许题目重复出现</span>
		</div>
		<div class="selectBox " style="margin-top: 0.4rem;">
			<div :class="['selectIcon',activeIndex==1?'active':'']" @click="change(1)">
				<img  v-if="activeIndex==1" src="../../../static/right.png" />
			</div>
			<span>只刷没做过的题目</span>
		</div>
		<p v-if="activeIndex==1" style="text-align: center;color: #fa8a12;font-size: 0.28rem;margin-top: 0.2rem;">剩余未做题目太少,不允许选择本项</p>
		<button class="comfirm_btn" style="background: #fa8a12;margin-top: 0.3rem;" @click="go">开始刷题</button>
		
	</div>
</template>

<script>
	
	export default{
		data(){
			return {
				list:{},
				activeIndex:0,
				type:1
				
			}
			
		},
		
		mounted(){
			
			this.getData()
			
		},
		methods:{
			go(){
				this.$router.push({name: 'Test',query:{PracticeType:this.activeIndex+1,Action:this.type}})
			},
			getData(){
				var obj={
					"s":this.$store.state.sessionId,
					"a":this.type
				}
				this.$Api.EveryExercise(obj).then((res)=>{
					
					this.list=res.q.exercise
				})
				
			},
			change(num){
				this.activeIndex=num
				
				
			}
		}
	}
	
</script>

<style scoped="scoped" lang="scss">
@import "../../sass/baseSass.scss";
.title_pic{
	width: 100%;
	height: 2.4rem;
	background: url(../../assets/test.png) no-repeat;
	background-size:100% 100%;
}
.title{
	padding: 0.3rem 0.3rem 0 0.3rem;
	font-size: 0.32rem;
}
.instruction{
	padding: 0.3rem;
	font-size: 0.3rem;
}
.little{
	color: #fa8a12;
	font-size: 0.28rem;
		padding: 0.2rem 0.3rem 0 0.3rem;
	
}
.selectBox{
	width: 3.5rem;
	height: 0.5rem;
	margin: auto;
	.selectIcon{
		width: 0.45rem;
		height: 0.45rem;
		float: left;
		border: 0.02rem solid #CCCCCC;
		border-radius:50% ;
	}
	.active{
		position: relative;
		background: #fa8a12;
		border: none;
		img{
			width: 0.24rem;
			height: 0.17rem;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			margin: auto;
		}
	}
	span{
		float: left;
		margin-left: 0.2rem;
		font-size: 0.3rem;
		line-height: 0.5rem;
	}
	
}
</style>


